.details{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 30px, 0;
}

.details .big-img{
    max-width: 500px;
    min-width: 290px;
    overflow: hidden;
    margin: 25px;
}

.big-img img{
    width: 100%;
    height: 100%;
    max-height: 400px;
    display: block;
    object-fit: cover;
}

.details .box{
    max-width: 500px;
    min-width: 290px;
    margin: 25px; 
}

.box .row{
    display: flex;
    justify-content: space-between;
}